        class="road_yellow"
      >
        <path
          class="road-1"
          d="M726.92,1340.61v848.87s-9.56,97.26,124.24,101.69h669s78.83.24,76.45-101.69c0,0,10.38-84.07-93.18-77.37H1073.37S973,2121,973,2014.84V1833.57s0-92.84,114.68-88.42h420.52s95.57-4.42,90.79-101.69v-283s-21.41-134.64,116.38-140.18H1924"
        />
        <path
          class="road-1"
          d="M478.79,0V181.27s9.56,97.26-105.13,106.11H168.18s-71.68,13.26-66.9,84c0,0,4.78,79.58,90.79,79.58H397.55s81.24,26.53,81.24,79.58c0,0,9.56,97.27-100.35,97.27H225.52s-71.68,13.26-71.68,97.26c0,0,14.34,70.74,95.58,79.58H832.05s95.57,8.85,95.57,84"
        />
        <circle class="road-1" cx="899.5" cy="1148.41" r="258" />
        <path class="road-2" d="M1222,1755.41v140s-10,59,87,64h563" />
        <path
          class="road-2"
          d="M0,1655.41H328s88-5,86,78v90s-4,73,82,74c0,0,228-8,220,0"
        />
        <path
          class="road-2"
          d="M1239,1386.41h176.84s63.79,11,65.06,109c0,0,4.42,58,57.47,73H1599"
        />
        <path
          class="road-2"
          d="M1008,1606.57h275.46c110.41,4.08,97-47.16,97-47.16v-92c.51,0,0-84,0-84"
        />
