      <svg
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 1927.5 2296.17"
        class="road_white"
      >
        <line class="cls-1" x1="479.79" y1="2.5" x2="479.79" y2="10" />
        <path
          class="cls-2"
          d="M479.79,30.14V183.77S489.35,281,374.66,289.88H169.18s-71.68,13.26-66.9,84c0,0,4.78,79.58,90.79,79.58H398.55S479.79,480,479.79,533c0,0,9.56,97.27-100.35,97.27H226.52s-71.68,13.26-71.68,97.26c0,0,14.34,70.74,95.58,79.58H833.05s80.31,7.43,93.69,66.57"
        />
        <path class="cls-1" d="M928.29,883.66q.33,3.63.33,7.5" />
        <circle class="cls-3" cx="900.5" cy="1150.91" r="258" />
        <path
          class="cls-4"
          d="M727.92,1343.11V2192s-9.56,97.26,124.24,101.69h669s78.83.24,76.45-101.69c0,0,10.38-84.07-93.18-77.37H1074.37S974,2123.45,974,2017.34V1836.07s0-92.84,114.68-88.42h420.52s95.57-4.42,90.79-101.69V1363s-21.41-134.64,116.38-140.18H1925"
        />
