          class="cls-5"
          d="M1009,1609.07h275.46c110.41,4.08,97-47.16,97-47.16v-92c.51,0,0-84,0-84"
        />
      </svg>
    </div>

    <Menu></Menu>
  </div>
</template>

<style scoped lang="css">
::-webkit-scrollbar {
  display: none;
}

div.road_part {
  transform: rotate(0deg);
  /* position: absolute; */
}

svg.road_yellow {
  position: absolute;
  z-index: 0;
}
svg.road_white {
  position: absolute;
  z-index: 1;
}

path.road-1,
path.road-2 {
  fill: none;
  stroke: #efcd24;
  stroke-miterlimit: 10;
}
circle.road-1 {
  fill: none;
  stroke: #efcd24;
  stroke-miterlimit: 10;
  stroke-width: 40px;
}
path.road-1 {
  stroke-width: 40px;
}
path.road-2 {
  stroke-width: 20px;
}

.cls-1,
.cls-2,
.cls-3,
.cls-4,
.cls-5 {
  fill: none;
  stroke: #fff;
  stroke-linecap: round;
  stroke-linejoin: round;
}
.cls-1,
.cls-2,
.cls-3,
.cls-4 {
  stroke-width: 5px;
}
.cls-2 {
  stroke-dasharray: 15.1 20.14;
}
.cls-3 {
  stroke-dasharray: 15.1 20.14;
}
.cls-4 {
  stroke-dasharray: 15 20;
}
.cls-5 {
  stroke-width: 2px;
  stroke-dasharray: 10 15;
}
</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";

//export default
export default {
  name: "Map",
  data() {
    return {
      global: global,
    };
  },
  components: {
    Menu,
  },
  beforeCreate() {
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {},
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    this.refresh_root_size();

    // show the loading anime
    this.show_loading_anime();
  },
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  destroyed() {},

  methods: {
    refresh_root_size() {
      let static_piexl = 0;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },

    show_loading_anime() {
      /* show_mode:
      0: default loading anime
      1: from home loading anime
       */
      let show_mode = 0;
      if (this.$route.params.show_mode) {
        show_mode = this.$route.params.show_mode;
      }

      switch (show_mode) {
        case 0:
          this.default_show_loading();
          break;
        case 1:
          this.from_home_loading();
          break;
      }
    },
    default_show_loading() {
      this.from_home_loading();
      console.log("这是默认加载动画");
    },
    from_home_loading() {
      anime({
        targets: [
          "path.road-1",
          "path.road-2",
          "circle.road-1",
          "path.cls-1",
          "path.cls-2",
          "circle.cls-3",
          "path.cls-4",
          "path.cls-5",
        ],
        easing: "linear",
        strokeDashoffset: [anime.setDashoffset, 0],
        duration: 5000,
      });

      console.log("这是从主页跳转过来的动画");
    },
  },
};
</script>


